import { FishComponent } from './components/fish/fish.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanadianFoodComponent } from './components/canadian-food/canadian-food.component';
import { CartComponent } from './components/cart/cart.component';
import { ChickenComponent } from './components/chicken/chicken.component';
import { HomeComponent } from './components/home/home.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  {path:'fish',component:FishComponent},
  { path: 'chicken', component: ChickenComponent },
  { path: 'canadian', component: CanadianFoodComponent },
  { path: 'cart', component: CartComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
