import { environment as env } from './../../environments/environment.prod';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private _httpclient:HttpClient) { }

  gethome():Observable<any>{
    return this._httpclient.get(`${env.apiUrl}i=chicken_breast`)
  }
  getCooking(): Observable<any> {
    return this._httpclient.get(`${env.apiUrl}c=Seafood`);
  }
  getCan(): Observable<any> {
    return this._httpclient.get(`${env.apiUrl}a=Canadian`);
  }

  getChicken(): Observable<any> {
    return this._httpclient.get(`${env.apiUrl}i=chicken`);
  }
}
